.App {
  margin: 0 auto;
  height: 100%;
  font-family: 'Roboto'; }

body, html {
  margin: 0;
  height: 100%; }

@media only screen and (max-width: 390px) {
  .header .header-wrap .right ul li button {
    font-size: 60%; } }

